.controlBarContainer{
    display: flex;
    margin-bottom: 2.5rem;
    justify-content: left;
    align-items: center;
}
select {
    width: 100px;
    margin: 0 1em;
    padding: 5px 2px;
    border: 2px solid #e68123;
}
.toggleCardDisplay{
    display: flex;
    margin-left: 3rem;
}
.toggleCardDisplay i {
    border: 1px solid #cbcbcb;
    padding: .25em .39em;
    cursor: pointer;
}
.selected {
    background-color: rgb(241, 241, 241);
}